.footer {

	@include min768 {
		position: relative;

		&::before {
			content: "";
			position: absolute;
			bottom: 0;
			right: 0;
			width: rem(183);
			height: rem(423);
			background: url(../img/footer_right.png) no-repeat;
		}
	}

	&__top {
		margin-top: rem(130);
		margin-bottom: rem(150);

		@include min540 {
			margin-top: rem(84);
			margin-bottom: rem(64);
		}
		

		.container {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			@include min768 {
				margin-left: 0;
				margin-right: 0;
			}
		}

		&_left {
			margin-top: rem(26);
			flex: 0 0 rem(250);
			max-width: rem(250);

			display: flex;
			flex-direction: column;

			@include min1200 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-top: 0;
				flex-direction: row;
				flex-wrap: wrap;

				margin-bottom: rem(36);
			}

			@include min768 {
				padding-left: 32px;
				padding-right: 32px;
			}

			.title-h2 {
				margin-bottom: rem(62);
				color: #474A51;

				@include min1200 {
					flex: 0 0 100%;
					max-width: 100%;
				}

				@include min768 {
					margin-bottom: rem(32);
				}
			}

			.block_list {
				display: flex;
				flex-direction: column;
				margin-bottom: rem(44);

				@include min1200 {
					flex: 0 0 33%;
					max-width: 33%;
				}

				@include min768 {
					flex: 0 0 50%;
					max-width: 50%;
					margin-bottom: rem(28);
				}

				@include min540 {
					&:first-of-type {
						flex: 0 0 rem(165);
						max-width: rem(165);
					}
					&:nth-of-type(2) {
						flex: 0 0 rem(145);
						max-width: rem(145);
						padding-left: rem(30);
					}
				}

				@include min375 {
					flex: 0 0 100%;
					max-width: 100%;
					
					&:nth-of-type(2) {
						padding-left: rem(20);
					}
				}

				@include min360 {
					&:nth-of-type(2) {
						padding-left: 0;
					}
				}

				&:last-child {
					margin-bottom: 0;
				}

				&__top {
					margin-bottom: rem(14);
					font-size: rem(14);
					color: #B2B2B2;

					@include min540 {
						font-size: rem(13);
					}
				}

				&__bottom {
					font-size: rem(16);
					line-height: 155%;
					color: var(--c-text2);

					@include min540 {
						font-size: rem(12);
					}
				}
			}
		}

		&_center {
			@include min1200 {
				flex: 0 0 45%;
				max-width: 45%;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			@include min768 {
				height: rem(278);

				flex: 0 0 100%;
				max-width: 100%;

				margin-bottom: rem(64);
			}
		}

		&_right {
			flex: 0 0 rem(300);
			max-width: rem(300);

			display: flex;
			flex-direction: column;
			justify-content: center;

			@include min1200 {
				flex: 0 0 45%;
				max-width: 45%;
			}

			@include min768 {
				padding-left: 32px;
				padding-right: 32px;

				flex: 0 0 100%;
				max-width: 100%;
			}

			.title {
				max-width: rem(297);
				margin-bottom: rem(40);

				font-size: rem(18);
				line-height: 146%;
				color: var(--c-text2);

				@include min540 {
					font-size: rem(14);
				}
			}

			.form {
				display: flex;
				flex-direction: column;

				.field-set {
					margin-bottom: rem(58);

					@include min768 {
						margin-bottom: rem(37);
					}
					
				}

				.field-button {
					display: flex;
					flex-direction: column;

					button {
						.last {
							display: none;
						}
					}

					@include min768 {
						button {
							.first {
								display: none;
							}
							.last {
								display: block;
							}
						}
					}

					> span {
						margin-top: rem(15);
						max-width: rem(238);
						margin-left: auto;
						margin-right: auto;
						text-align: center;

						font-size: rem(12);
						line-height: 136%;
						color: #ADADB2;
					}
				}
			}
		}
	}

	&__bottom {
		background-color: white;
		padding-top: rem(42);
		padding-bottom: rem(42);

		@include min768 {
			padding-top: rem(56);
			padding-bottom: rem(56);

			
		}

		.logo {
			margin-top: rem(4);

			@include min768 {
				margin-top: 0;
				margin-bottom: rem(48);
			}
		}

		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;

			@include min768 {
				flex-direction: column;
				justify-content: center;
			}
		}

		.copy {
			text-align: center;

			color: #AAAAAA;
			font-size: rem(12);

			@include min768 {
				margin-bottom: rem(48);
			}
		}

		.right {
			color: #AAAAAA;
			font-size: rem(12);
			text-decoration: underline;

			&:hover {
				text-decoration: none;
			}
		}
	}

}