.map {

	position: relative;

	@include min768 {
		&::before {
			content: "";
			position: absolute;
			right: 0;
			top: rem(-142);
			background: url(../img/map_right_smart.png) no-repeat;

			width: rem(157);
			height: rem(321);
		}
	}

	&__top {
		.title-h2 {
			color: var(--c-text);
			margin-bottom: rem(64);

			@include min768 {
				margin-bottom: rem(24);
			}
		}

		&_items {
			display: flex;
			justify-content: space-between;
			margin-bottom: rem(70);

			@include min {
				flex-wrap: wrap;
				margin-bottom: rem(35);
			}

			.item {
				display: flex;
				flex-direction: column;

				padding-right: rem(40);
				padding-left: rem(40);

				border-right: rem(1) solid rgba(86,86,92,.2);

				@include min1050 {
					padding-right: rem(20);
					padding-left: rem(20);
				}

				@include min {
					border-right: 0;

					flex: 0 0 48%;
					max-width: 48%;

					padding-right: 0;
					padding-left: 0;

					margin-bottom: rem(35);
				}

				@include min540 {
					&:first-child {
						order: 0;
						
					}

					&:nth-child(2) {
						order: 1;
					}

					&:nth-child(3) {
						order: 2;
					}

					&:nth-child(4) {
						order: 4;
					}

					&:nth-child(5) {
						order: 3;
					}
				}

				&:last-child {
					padding-right: 0;
					border-right: 0;
				}

				&:first-child {
					padding-left: 0;
				}

				&__first {
					margin-bottom: rem(4);
					white-space: nowrap;
					color: #28282E;
					font-size: rem(24);

					span {
						font-family: var(--f-sup);
						font-size: rem(40);

						@include min540 {
							font-size: rem(32);
						}
						
					}
				}

				&__last {
					color: #8E8E93;
					font-size: rem(14);
					line-height: 120%;

					max-width: rem(182);

					@include min540 {
						font-size: rem(12);
						max-width: rem(147);
					}
				}
			}
		}
	}

	&__bottom {
		position: relative;
		height: rem(886);

		@include min1200 {
			overflow-x: auto;
		}

		@include min768 {
			height: rem(434);
			overflow-x: hidden;
			overflow-y: hidden;
		}

		&::before {
			content: "";

			position: absolute;
			bottom: rem(-364);
			left: 0;
			z-index: 2;

			width: rem(496);
			height: rem(880);

			background: url(../img/map/left.png) no-repeat;

			@include min1200 {
				display: none;
			}

		}

		.overlay {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			@include min1200 {
				width: 1207px;
			}

			@include min768 {
				width: 100%;
				height: rem(454);
				margin-right: rem(-173);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;

				@include min768 {
					height: calc(100% - 20px);
					object-position: -379px;
				}
			}
		}

		&_items {
			position: absolute;
			z-index: 2;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			.container {
				position: relative;
				height: rem(886);

				@include min768 {
					height: rem(414);
				}
			}

			.item1 {
				bottom: rem(786);
				left: rem(474);

				@include min768 {
					left: rem(8);
					bottom: rem(358);
				}
			}

			.item2 {
				bottom: rem(683);
				left: rem(983);

				@include min768 {
					left: rem(226);
					bottom: rem(317);
				}
			}

			.item3 {
				bottom: rem(496);
				left: rem(564);

				@include min768 {
					left: rem(56);
					bottom: rem(218);
				}
			}

			.item4 {
				bottom: rem(395);
				left: rem(549);

				@include min768 {
					left: rem(46);
					bottom: rem(158);
				}
			}

			.item5 {
				bottom: rem(255);
				left: rem(651);

				@include min768 {
					left: rem(146);
					bottom: rem(100);
				}
			}

			.item6 {
				bottom: rem(28);
				left: rem(738);

				@include min768 {
					left: rem(174);
					bottom: rem(4);
				}
			}

			.item {
				position: absolute;
				display: flex;
				flex-direction: column;
				padding: rem(16) rem(24);
				background-color: #433632;

				transition: var(--transition);

				@include min768 {
					padding: rem(8) rem(10);
				}

				&:hover {
					// padding: rem(16) rem(24) rem(16) rem(52);
					background-color: #C97A66;

					// .item__abs {
					// 	opacity: 0;
					// }

					.item__text,
					.item__subtext {
						color: white;
					}

					// svg {
					// 	opacity: 1;
					// }
				}

				svg {
					position: absolute;
					top: rem(-2);
					left: rem(-20);
					z-index: 2;

					transition: var(--transition);
					opacity: 0;

					@include min768 {
						width: rem(41) !important;
						height: rem(53) !important;

						top: rem(-8);
						left: rem(-24);
					}
				}

				&__abs {
					position: absolute;
					top: rem(-12);
					left: rem(-12);
					width: rem(24);
					height: rem(24);
					border-radius: 50%;
					background-color: rgba(255,255,255,.87);

					display: flex;
					align-items: center;
					justify-content: center;
					opacity: 1;
					transition: var(--transition);

					@include min768 {
						width: rem(12);
						height: rem(12);
						top: rem(-6);
						left: rem(-6);
					}

					&::before {
						content: '';
						width: rem(16);
						height: rem(16);
						border-radius: 50%;
						background-color: var(--c-accent);

						@include min768 {
							width: rem(8);
							height: rem(8);
						}
					}
				}

				&__text {
					color: white;
					line-height: 120%;

					@include min768 {
						white-space: nowrap;
						font-size: rem(12);
					}
				}

				&__subtext {
					font-size: rem(13);
					line-height: 120%;
					color: #9196A2;

					@include min768 {
						white-space: nowrap;
					}
				}
			}

			.item.active {
				padding: rem(16) rem(24) rem(16) rem(52);
				background-color: #C97A66;

				@include min768 {
					padding: rem(8) rem(10) rem(8) rem(22);
				}

				.item__abs {
					opacity: 0;
				}

				.item__text,
				.item__subtext {
					color: white;
				}

				svg {
					opacity: 1;
				}
			}
		}
	}

}