.message {
	margin-bottom: rem(120);

	@include min768 {
		margin-bottom: rem(70);
		position: relative;
	}

	&::before {
		@include min768 {
			content: "";
			position: absolute;
			top: rem(-140);
			right: 0;
			background: url(../img/message_right_smart.png) no-repeat;

			width: rem(178);
			height: rem(327);
		}
	}

	.title-h2 {
		max-width: rem(700);
		margin-bottom: rem(42);
		color: #474A51;

		@include min768 {
			margin-bottom: rem(22);
		}
	}

	.text {
		margin-bottom: rem(44);
		font-size: rem(18);
		line-height: 146%;
		color: var(--c-text2);

		@include min1050 {
			text-align: center;
		}

		@include min768 {
			text-align: left;
			font-size: rem(14);
		}
	}

	.form {
		display: flex;
		align-items: center;
		// justify-content: space-between;

		@include min1050 {
			flex-direction: column;
			
		}

		.field-set {
			flex: 0 0 rem(329);
			max-width: rem(329);

			margin-right: rem(42);

			@include min1050 {
				flex: 1;
				width: 50%;
				max-width: 50%;
				margin-bottom: rem(36);
				
				margin-right: 0;
			}

			@include min768 {
				width: 100%;
				max-width: 100%;
			}
		}

		.field-button {
			display: flex;
			flex-direction: column;
			align-items: center;

			flex: 0 0 rem(294);

			.last {
				display: none;

				@include min540 {
					display: block;
				}
			}

			@include min540 {
				.first {
					display: none;
				}
			}

			@include min1050 {
				flex: 1;
			}

			@include min768 {
				width: 100%;
				max-width: 100%;
			}

			button {
				width: 100%;

				@include min1050 {
					display: flex;
					justify-content: center;
				}
			}

			> span {
				margin-top: rem(10);
				font-size: rem(12);
				line-height: 138%;
				color: #ADADB2;
				text-align: center;
			}
		}
	}

}