.layouts {
	position: relative;
	background-color: #433632;
	padding-top: rem(222);
	padding-bottom: rem(93);

	@include min1050 {
		padding-top: rem(70);
		padding-bottom: rem(46);
	}

	&__head {

		display: flex;
		justify-content: center;

		@include min768 {
			justify-content: space-between;
			align-items: center;

			margin-left: 32px;
			margin-right: 32px;

			margin-bottom: rem(40);
		}

		.page_count {
			display: none;

			@include min768 {
				display: flex;
				align-items: baseline;

				.first {
					color: var(--c-accent);
					font-family: var(--f-viaoda);
					font-size: rem(21);
				}

				.last {
					font-family: var(--f-viaoda);
					color: #727781;
					font-size: rem(16);

					&::before {
						content: "/";
					}
				}
			}
		}
	}

	.title-h2 {
		// text-transform: uppercase;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
		color: white;
		margin-bottom: rem(72);

		@include min768 {
			margin-bottom: 0;
			margin-left: 0;
			margin-right: 0;
		}
	}

	.overlay {
		position: absolute;
		top: 0;
		height: 100%;
		width: 100%;

		@include min768 {
			display: none;
		}

		.container {
			position: relative;
			height: 100%;
			width: 100%;
		}
	}

	.slider_nav {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: rem(613);
		right: rem(240);
		z-index: 10;


		width: rem(120);
		height: rem(120);
		border-radius: 50%;

		border: rem(1) solid var(--c-accent);

		@include min768 {
			display: none;
		}

		&:hover {
			.slider_nav__prev {
				margin-left: rem(-12);
			}

			.slider_nav__next {
				margin-right: rem(-12);
			}
		}

		&__count {
			span {
				font-family: var(--f-viaoda);
			}

			.first {
				font-size: rem(21);
				color: var(--c-accent);
			}

			.last {
				font-size: rem(16);
				color: #727781;

				&::before {
					content: "/";
				}
			}
		}

		&__prev,
		&__next {
			position: absolute;
			top: 50%;
			margin-top: rem(-6);
			transition: var(--transition);
		}

		&__prev {
			left: 0;
			margin-left: rem(12);
		}

		&__next {
			right: 0;
			margin-right: rem(12);
		}
	}

	.slider_layout {

		@include max768 {
			.swiper-wrapper {
				align-items: center;
			}

			.swiper-slide {
				width: rem(754);
				height: rem(626);

				@include min1440 {
					width: rem(654);
					height: rem(526);
				}

				.layouts__item {
					height: 100%;

					&_bottom {
						display: none;
					}
				}
			}

			.swiper-slide-active {
				height: rem(798);

				@include min1440 {
					height: rem(698);
				}

				.layouts__item {
					height: 100%;

					&_bottom {
						display: flex;
					}
				}
			}
		}

		@include min768 {

			.swiper-slide {

				.layouts__item {

					&_bottom {
						display: none;
					}
				}
			}

			.swiper-slide-active {

				.layouts__item {

					&_bottom {
						display: flex;
					}
				}
			}
		}

		
	}

	&__item {
		background-color: white;
		padding: rem(20) rem(40) rem(40) rem(40);

		transition: var(--transition);

		display: flex;
		flex-direction: column;

		@include min768 {
			background-color: transparent;
			padding: 0;
		}

		&_image {
			height: 100%;
			max-height: rem(523);
			margin: 0 rem(120);

			display: flex;
			align-items: center;
			justify-content: center;

			@include min768 {
				margin: 0;
				background-color: white;
				padding: rem(24);
				margin-bottom: rem(38);
			}

			@include min768 {
				max-height: rem(330);
				height: rem(330);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;

				@include min768 {
					margin-bottom: rem(24);
				}

				@include min540 {
					width: auto;
					height: 100%;
					// object-fit: none;
				}
			}
		}

		&_bottom {
			margin-top: auto;

			display: flex;
			justify-content: space-between;

			@include min768 {
				flex-direction: column;
			}

			.left {
				display: flex;
				flex-direction: column;

				flex: 0 0 55%;
				max-width: 55%;

				@include min1440 {
					flex: 0 0 50%;
					max-width: 50%;
				}

				@include min768 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-bottom: rem(18);
				}

				.title-h5 {
					// margin-bottom: rem(22);
					text-transform: uppercase;
					color: var(--c-text2);
					margin-bottom: rem(20);

					@include min768 {
						margin-bottom: rem(12);
						color: white;
					}
				}

				p {
					font-size: rem(14);
					color: var(--c-text2);

					@include min768 {
						font-size: rem(13);
						color: white;
					}
				}
			}

			.right {
				display: flex;
				flex-direction: column;

				flex: 0 0 40%;
				max-width: 40%;

				@include min1440 {
					flex: 0 0 45%;
					max-width: 45%;
				}

				@include min768 {
					flex: 0 0 100%;
					max-width: 100%;
				}

				> span {
					font-size: rem(16);
					line-height: 120%;
					color: #A5A5A5;
					margin-bottom: rem(18);

					@include min768 {
						font-size: rem(14);
					}
				}

				.button_accent {
					display: flex;
					justify-content: center;
					span {
						white-space: nowrap;
					}
				}
			}
		}
	}
}