.gallery {
	position: relative;
	background-color: #433632;
	padding-top: rem(92);
	margin-bottom: rem(132);

	&::before {
		content: "";
		position: absolute;
		top: rem(-2);
		left: 0;
		width: 100%;
		height: rem(3);
		background-color: #433632;
	}

	@include min768 {
		padding-top: rem(46);


	}

	&__top {
		margin-bottom: rem(62);

		display: flex;
		align-items: center;
		justify-content: space-between;

		@include min768 {
			margin-bottom: rem(32);
		}

		.title-h2 {
			color: white;
		}

		.page_control {

			&__prev,
			&__next {
				svg {
					path {
						stroke: white;
					}
				}

				@include min768 {
					display: none;
				}
			}

			.line {
				width: rem(170);

				@include min768 {
					display: none;
				}
			}

			&__count {
				@include min768 {
					.first {
						font-size: rem(21);
					}

					.last {
						font-size: rem(16);
					}
				}
			}
		}
	}

	&__bottom {
		height: rem(622);

		@include min768 {
			height: rem(422);
		}

		@include min540 {
			height: rem(210);
		}

		.swiper {
			margin-bottom: rem(-22);
			padding-bottom: rem(22);

			@include min540 {
				margin-bottom: rem(-70);
				padding-bottom: rem(70);
			}
		}

		.swiper-slide {
			position: relative;
			width: 1146px;
			height: rem(622);

			@include min1200 {
				width: 100%;
			}

			@include min768 {
				height: rem(422);
			}

			@include min540 {
				height: rem(210);
			}

			.text {
				position: absolute;
				bottom: rem(-22);
				right: rem(22);

				background-color: rgba(176, 104, 85, .96);
				padding: rem(32);
				width: rem(273);

				font-size: rem(14);
				line-height: 152%;
				color: white;

				opacity: 0;

				@include min1050 {
					right: 50%;
					margin-right: rem(-137);
				}

				@include min540 {
					bottom: rem(-70);
					padding: rem(20);

					font-size: rem(12);
					
					width: rem(216);
					margin-right: rem(-108);
				}
			}

			&::before {
				position: absolute;
				content: "";
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: rgba(134,112,106,.6);

				opacity: 1;

				transition: var(--transition);
			}
		}

		.swiper-slide-active {
			&::before {
				opacity: 0;
			}

			.text {
				opacity: 1;
			}
		}

		a {
			width: 100%;
			height: 100%;
			display: block;
		}

		img {
			width: 100%;
			height: rem(622);
			object-fit: cover;

			@include min768 {
				height: rem(422);
			}

			@include min540 {
				height: rem(220);
			}
		}
	}
}