.investment {
	position: relative;
	padding-top: rem(154);
	padding-bottom: rem(77);
	
	background: url(../img/investment/bg.jpg) no-repeat top center;
	background-size: cover;

	@include min1050 {
		padding-top: rem(70);
		padding-bottom: rem(53);
	}

	@include min768 {
		background: url(../img/investment/bg_smart.jpg) no-repeat top center;
		background-size: cover;
	}
	
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(50, 41, 38, .97);
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	.list {
		display: flex;
		padding: rem(16) 0;
		border-top: rem(1) solid #6E7380;

		@include min540 {
			border-top: 0;
		}
		
		&__count {
			font-size: rem(16);
			color: white;
			font-family: var(--f-viaoda);
			flex: 0 0 rem(50);
			max-width: rem(50);

			@include min540 {
				font-size: rem(14);

				flex: 0 0 rem(25);
				max-width: rem(25);
			}
		}

		&__text {
			font-size: rem(16);
			font-weight: 500;
			line-height: 152%;
			color: #B5B5B5;

			@include min540 {
				font-size: rem(13);
			}
		}

	}

	&__left,
	&__right {
		position: relative;
		z-index: 2;
	}

	&__left {
		margin-top: rem(36);
		flex: 0 0 rem(415);
		max-width: rem(415);
	
		display: flex;
		flex-direction: column;

		.smart {
			display: none;
		}

		@include min1050 {
			margin-top: 0;
			flex: 0 0 100%;
			max-width: 100%;

			.smart {
				display: block;

				img {
					max-width: rem(540);
					width: 100%;
					height: 100%;
					object-fit: cover;
					display: block;
				}

				p {
					&:last-of-type {
						margin-bottom: 0;
					}
				}

				.list {
					margin-bottom: rem(20);
				}
			}

			.page_control {
				margin-top: rem(24);
			}
		}

		.swiper_left {
			width: 100%;

			@include min1050 {
				padding: rem(20);
				background-color: #493C39;
			}
		}

		.title-h2 {
			color: white;
			margin-bottom: rem(48);

			@include min768 {
				margin-bottom: rem(22);
			}
		}

		.title {
			margin-bottom: rem(8);
			font-size: rem(16);
			font-weight: 500;
			color: white;
			line-height: 152%;
			display: block;

			@include min768 {
				font-size: rem(14);
				margin-bottom: rem(6);
			}
		}

		.text {
			font-size: rem(16);
			color: #BCBCC1;
			line-height: 152%;
			margin-bottom: rem(40);

			@include min768 {
				font-size: rem(12);
				margin-bottom: rem(28);
			}
		}

		.page_control {
			&__prev,
			&__next {
				svg {
					path {
						stroke: white;
					}
				}
			}
		}
	}

	&__right {
		margin-left: rem(82);
		flex: 1;
		min-width: 0;

		@include min1050 {
			display: none;
		}

		.swiper_right {
			width: 100%;
		}

		.slide1 {
			flex: 0 0 rem(632);
			max-width: rem(632);
		}

		.slide2 {
			margin-top: rem(184);
			display: flex;
			flex-direction: column;
			
			.title {
				margin-bottom: rem(11);
				font-size: rem(16);
				font-weight: 500;
				color: white;
				line-height: 152%;
				max-width: rem(210);
			}

			.text {
				font-size: rem(16);
				color: #BCBCC1;
				line-height: 152%;
				max-width: rem(530);
				// margin-bottom: rem(40);
			}
		}

		.slide3 {
			img {
				margin-bottom: rem(42);
			}

			.list {
				max-width: rem(348);
			}
		}
	}
}