.info {
	position: relative;
	z-index: 3;
	margin-bottom: rem(120);

	@include min768 {
		margin-bottom: rem(70);
	}

	&::before {
		content: "";
		position: absolute;
		top: rem(-160);
		left: 0;

		width: rem(342);
		height: rem(636);

		background: url(../img/section2/left.png) no-repeat top center;

		@include min1440 {
			top: rem(-240);
			// left: rem(-64);
		}

		@include min1200 {
			display: none;
		}
	}

	.container {
		display: flex;
		// flex-wrap: wrap;

		// justify-content: space-between;

		@include min1200 {
			flex-wrap: wrap;

		}
	}

	&__wrap_tab {
		display: none;
	}

	&__wrap_tab.active {
		display: flex;

		@include min1050 {
			flex-wrap: wrap;
			min-width: 0;
		}
	}

	.tab_1 {
		margin-left: rem(77);

		@include min1050 {
			margin-left: 0;
		}
	}

	.tab_2 {
		margin-left: rem(34);

		@include min1050 {
			margin-left: 0;
		}
	}

	.tab_3 {
		margin-left: rem(68);

		@include min1050 {
			margin-left: 0;
		}
	}

	&__tab {
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		align-self: flex-start;

		// margin-right: rem(77);
		margin-left: rem(6);

		padding-left: rem(37);

		border-left: rem(1) solid #E1C9B3;

		margin-top: rem(90);

		@include min1200 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-top: 0;
			padding-left: 0;
			margin-bottom: rem(60);

			flex-direction: row;
			border-left: 0;
			border-bottom: rem(1) solid #E1C9B3;
		}

		@include min540 {
			justify-content: space-between;
		}

		.item {
			position: relative;
			margin-bottom: rem(24);
			height: rem(61);

			display: flex;
			align-items: center;

			@include min1200 {
				margin-bottom: rem(8);
				margin-right: rem(40);
			}

			@include min540 {
				// flex-grow: 1;
				// min-width: 0;

				margin-right: auto;
			}

			&:last-child {
				margin-bottom: 0;

				@include min1200 {
					margin-right: 0;
				}
			}

			&:hover {
				.text {
					color: var(--c-accent);
				}
			}

			.line {
				position: absolute;
				top: 0;
				left: rem(-39);
				height: 100%;
				width: rem(2);
				background-color: var(--c-accent);

				opacity: 0;

				@include min1200 {
					left: 0;
					top: auto;
					bottom: rem(-8);
					width: 100%;
					height: rem(2);
				}

				&::before {
					content: "";
					position: absolute;
					top: 50%;
					margin-top: rem(-6);
					left: 0;

					width: 0;
					height: 0;

					
					border: rem(6) solid transparent;

					border-right: 0;
					border-left-color: var(--c-accent);

					@include min1200 {
						left: 50%;
						margin-left: rem(-3);
						margin-top: rem(-8);
						transform: rotate(-90deg);
					}
				}
			}

			.text {
				font-size: rem(12);
				letter-spacing: rem(5);
				text-transform: uppercase;
				color: var(--c-text);

				@include min540 {
					letter-spacing: rem(1.2);
				}
			}
		}

		.item.active {

			.line {
				opacity: 1;
			}
			
			.text {
				color: #B26855;
				font-weight: 600;
				font-size: rem(14);

				@include min540 {
					font-size: rem(12);
				}
			}
		}
	}

	&__center {
		flex: 0 0 rem(420);
		max-width: rem(420);

		@include min1050 {
			flex: 0 0 100%;
			max-width: 100%;

			display: flex;
			justify-content: center;

			order: 1;

			margin-top: rem(30);
		}

		@include min540 {
			width: 100vw;
			flex: 0 0 100vw;
			max-width: 100vw;
			position: relative;
			left: 50%;
			margin-left: -50vw;

			img {
				width: 100%;
			}
		}
	}

	&__text {
		margin-top: rem(42);
		flex: 0 0 rem(373);
		max-width: rem(373);
		// flex-grow: 1;
		min-width: 0;

		display: flex;
		flex-direction: column;

		margin-left: rem(70);

		@include min1050 {
			margin-left: 0;
			margin-top: 0;

			flex: 0 0 100%;
			max-width: 100%;
		}

		.swiper {
			width: 100%;
		}

		&_head {
			margin-bottom: rem(52);
			display: flex;
			flex-direction: column;

			@include min1050 {
				margin-bottom: rem(20);
			}

			.subtitle {
				font-size: rem(10);
				line-height: 140%;
				text-transform: uppercase;
				color: var(--c-accent2);

				margin-bottom: rem(17);

				@include min768 {
					margin-bottom: rem(12);
				}
			}

			.title-h2 {
				color: var(--c-text);
			}
		}

		&_p {
			position: relative;
			margin-bottom: rem(83);

			margin-left: rem(30);

			@include min1050 {
				margin-left: 0;
				margin-bottom: rem(50);
			}

			&::before {
				content: "";
				position: absolute;
				top: rem(10);
				left: rem(-132);
				background-color: var(--c-accent2);
				height: rem(1);
				width: rem(114);

				@include min1050 {
					display: none;
				}
			}

			p {
				color: var(--c-text);
				line-height: 140%;
				margin-bottom: rem(28);

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		.page_control {
			margin-right: rem(48);

			@include min768 {
				margin-right: 0;
			}
		}
	}

	

	&__list {

		flex: 0 0 rem(754);
		max-width: rem(754);

		// @include min1440 {
		// 	flex: 0 0 rem(674);
		// 	max-width: rem(674);
		// }
		
		@include min768 {
			flex: 0 0 100%;
			max-width: 100%;
		}

		.smart {
			display: none;

			@include min768 {
				font-size: rem(10);
				line-height: 140%;
				text-transform: uppercase;
				color: var(--c-accent2);

				margin-bottom: rem(12);
				display: block;
			}


		}

		&_head {
			.title-h2 {
				margin-bottom: rem(24);
				color: var(--c-text);
			}

			p {
				color: var(--c-text2);
				line-height: 140%;
				max-width: rem(288);

				@include min768 {
					max-width: 100%;
				}
			}
		}

		&_items {
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
			height: rem(425);

			// @include min1440 {
			// 	height: rem(524);
			// }

			@include min768 {
				height: auto;
			}
		}

		&_item {
			display: flex;
			padding-top: rem(12);
			margin-bottom: rem(32);
			max-width: rem(362);
			border-top: rem(1) solid #DAD5D1;

			// @include min1440 {
			// 	max-width: rem(282);
			// }

			@include min768 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-bottom: rem(25);
			}

			&:first-child {
				margin-right: rem(30);
				border-top: 0;

				@include min768 {
					margin-right: 0;
				}

				.text {
					max-width: rem(288);

					@include min768 {
						max-width: 100%;
					}
				}
			}

			&:nth-child(2) {
				margin-right: rem(30);

				@include min768 {
					margin-right: 0;
				}
			}

			&:nth-child(3) {
				margin-right: rem(30);

				@include min768 {
					margin-right: 0;
				}
			}

			&:nth-child(4) {
				margin-bottom: 0;
				margin-right: rem(30);

				@include min768 {
					margin-right: 0;
					margin-bottom: rem(25);
				}
			}

			&:nth-child(5) {
				margin-top: rem(59);

				// @include min1440 {
				// 	margin-top: rem(80);
				// }

				@include min768 {
					margin-top: 0;
				}
			}

			&:last-child {
				margin-bottom: 0;
			}

			.count {
				margin-right: rem(20);
				font-family: var(--f-sup);
				color: var(--c-text2);

				@include min768 {
					margin-top: rem(3);
				}
			}

			.text {
				color: var(--c-text2);
				font-size: rem(16);
			}
		}

	}

	&__right_image {
		flex: 0 0 rem(425);
		max-width: rem(425);
		margin-left: rem(48);
		// height: rem(5);

		@include min1440 {
			margin-left: rem(24);

			flex: 0 0 rem(362);
			max-width: rem(362);
		}

		@include min1050 {
			flex: 0 0 100%;
			max-width: 100%;

			display: flex;
			justify-content: center;

			margin-top: rem(32);
		}

		@include min768 {
			display: none;
		}
	}

	&__left_image {
		flex: 0 0 rem(598);
		max-width: rem(598);

		margin-right: rem(46);

		@include min1440 {
			flex: 0 0 rem(498);
			max-width: rem(498);
		}

		@include min {
			flex: 0 0 100%;
			max-width: 100%;
			order: 2;

			display: flex;
			justify-content: center;

			margin-top: rem(40);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;

			@include min {
				width: auto;
				height: auto;
			}
		}
	}

	&__blocks {
		display: flex;
		flex-direction: column;

		justify-content: center;

		@include min {
			flex: 0 0 100%;
			max-width: 100%;
		}

		.title-h2 {
			color: var(--c-text);
			margin-bottom: rem(81);

			@include min {
				margin-bottom: rem(20);
			}
		}

		.items {
			display: flex;
			flex-wrap: wrap;

			

			.item {
				flex: 0 0 50%;
				max-width: 50%;

				margin-bottom: rem(42);

				@include min {
					flex: 0 0 25%;
					max-width: 25%;

					margin-bottom: 0;
				}

				@include min768 {
					flex: 0 0 50%;
					max-width: 50%;

					&:first-child, &:nth-child(2) {
						margin-bottom: rem(20);
					}
				}

				&:nth-child(3), &:last-child {
					margin-bottom: 0;
				}

				&__top {
					.text {
						font-family: var(--f-sup);
						font-size: rem(36);
						color: var(--c-text);

						span {
							margin-left: rem(4);
							font-size: rem(14);
							text-transform: uppercase;
						}
					}
				}

				&__bottom {
					span {
						font-size: rem(14);
						color: #AAAAAA;
					}
				}
			}
		}
	}
}